import React, { Component } from "react";
import Newsitem from "./Newsitem";
import Spinner from "./Spinner";
import PropTypes from 'prop-types'
import InfiniteScroll from 'react-infinite-scroll-component';

export default class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      loading: true,
      page: 1,
      total:0
    };
    document.title=`NewsGG - ${this.props.topic}`;
  }
  static defaultProps = {
    countries: "GB", 
    topic: "news"
  }
  static propTypes = {
    countries: PropTypes.string,
    topic: PropTypes.string
  }
  async componentDidMount() {
    this.props.setProgress(10);
    let url = `https://api.newscatcherapi.com/v2/latest_headlines?countries=${this.props.countries}&topic=${this.props.topic}&page=1&page_size=${this.props.pageSize}&lang=en`;
    //https://api.newscatcherapi.com/v2/latest_headlines?topic=sport&countries=GB&page=201
    //https://newsdata.io/api/1/news?apikey=pub_67140ee96c3b16103c1c340ae58d50448859&q=pegasus&language=en
    this.setState({ loading: true });
    let data = await fetch(url,{headers: {"X-API-KEY": "8p6o2LXOlAsteInGk1AUZAcd6Hj5HMpYonGbL_RZNVs"}});
    this.props.setProgress(25);
    let ParsedData = await data.json();
    this.props.setProgress(50);
    this.setState({
      articles: ParsedData.articles,
      total: ParsedData.total_hits,
      loading: false,
    });
    this.props.setProgress(100);
  }
  // handlePrevious = async () => {
  //   let url = `https://newsapi.org/v2/top-headlines?countries=${this.props.countries}&topic=${this.props.topic}&apiKey=e0517521bb2a4a849fdd29c7a125160a&page=${
  //     this.state.page - 1
  //   }&pageSize=${this.props.pageSize}`;
  //   this.setState({ loading: true });
  //   let data = await fetch(url);
  //   let ParsedData = await data.json();
  //   this.setState({
  //     page: this.state.page - 1,
  //     articles: ParsedData.articles,
  //     loading: false,
  //   });
  // };
  // handleNext = async () => {
  //   let url = `https://newsapi.org/v2/top-headlines?countries=${this.props.countries}&topic=${this.props.topic}&apiKey=e0517521bb2a4a849fdd29c7a125160a&page=${
  //     this.state.page + 1
  //   }&pageSize=${this.props.pageSize}`;
  //   this.setState({ loading: true });
  //   let data = await fetch(url);
  //   let ParsedData = await data.json();
  //   this.setState({
  //     page: this.state.page + 1,
  //     articles: ParsedData.articles,
  //     loading: false,
  //   });
  // };
  fetchMoreData = async() => {
    this.setState({page:this.state.page+1})
    const url =  `https://api.newscatcherapi.com/v2/latest_headlines?countries=${this.props.countries}&topic=${this.props.topic}&page=${this.state.page}&page_size=${this.props.pageSize}&lang=en`;
    let data = await fetch(url,{headers: {"X-API-KEY": "8p6o2LXOlAsteInGk1AUZAcd6Hj5HMpYonGbL_RZNVs"}});
    let ParsedData = await data.json();
    this.setState({
      articles: this.state.articles.concat(ParsedData.articles),
      total: ParsedData.total_hits
    });
  }
  render() {
    return (
      <>
        
        <div className="text-center">{this.state.loading && <Spinner />}</div>
        <InfiniteScroll
          dataLength={this.state.articles.length}
          next={this.fetchMoreData}
          hasMore={this.state.articles.length!==this.state.total}
          loader={<Spinner/>}
        >
          <div className="container">
        <div className="row">
          {!this.state.loading &&
            this.state.articles.map((element) => {
              return (
                <div className="col-md-4" key={element.url}>
                  <Newsitem
                    title={element.title}
                    summary={element.summary}
                    media={element.media}
                    link={element.link}
                    author={element.author}
                    date={element.published_date}
                  />
                </div>
              );
            })}
        </div>
        </div>
        </InfiniteScroll>
        {/* <div className="container d-flex justify-content-between">
          <button
            disabled={this.state.page <= 1}
            type="button"
            className="btn btn-info"
            onClick={this.handlePrevious}
          >
            &larr;Previous
          </button>
          <button
            disabled={
              this.state.page + 1 >
              Math.ceil(this.state.total / this.props.pageSize)
            }
            type="button"
            className="btn btn-info"
            onClick={this.handleNext}
          >
            Next&rarr;
          </button>
        </div> */}
      </>
    );
  }
}
