import React, { Component } from "react";
import { Link } from "react-router-dom";


export default class Navbar extends Component {
  state={
    color:'black',
    btntext:'off'
  }
  

  toggleMode = () => {
   
    if (this.state.color==='black'){
    document.body.style.backgroundColor='#1F1B24'
    this.setState({color:'white',btntext:'on'})
    
    }
    else{

      document.body.style.backgroundColor='white';
      this.setState({color:'black',btntext:'off'})
      
    }
  };
   
 

  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
          <div className="container-fluid">
            <Link className="navbar-brand" to="/">
              NewsGG
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <Link className="nav-link" aria-current="page" to="/">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Business">
                    Business
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Entertainment">
                    Entertainment
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Health">
                    Beauty
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Science">
                    Science
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Sports">
                    Sports
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/Technology">
                    Technology
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          <div className="form-check form-switch" >
            <input
              className="form-check-input"
              onClick={this.toggleMode}
              type="checkbox"
              role="switch"
              id="flexSwitchCheckDefault" 
            />
            <label
              className="form-check-label"
              htmlFor="flexSwitchCheckDefault "
               style={{color:"white"}}
              
            ><strong>Night mode {this.state.btntext}</strong>
              
            </label>
          </div>
        </nav>
        <h1 className="text-center my-2" style={this.state}>NewsGG Top Headlines</h1>
      </div>
    );
  }
}
