import './App.css';
import React, { Component } from 'react'
import Navbar from './components/Navbar';
import News  from './components/News';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import LoadingBar from 'react-top-loading-bar'

export default class App extends Component {
  state={
    progress:0
  }
  setProgress=(progress)=>{
    this.setState({progress:progress})
    
  }
  render() {
    return (
      <div>
        <Router>
        <Navbar/>
        <LoadingBar
        color='#1F51FF'
        progress={this.state.progress}
        
      />
        
        
        
        <Switch>
          <Route exact path="/">
            <News setProgress={this.setProgress} key="news" pageSize={6} countries="in" topic="news"/>
          </Route>
          <Route exact path="/Business">
            <News setProgress={this.setProgress} key="business" pageSize={6} countries="in" topic="business"/>
          </Route>
          <Route exact path="/Entertainment">
            <News setProgress={this.setProgress} key="entertainment" pageSize={6} countries="in" topic="entertainment"/>
          </Route>
          <Route exact path="/Health">
            <News setProgress={this.setProgress} key="beauty" pageSize={6} countries="in" topic="beauty"/>
          </Route>
          <Route exact path="/Science">
            <News setProgress={this.setProgress} key="science" pageSize={6} countries="in" topic="science"/>
          </Route>
          <Route exact path="/Sports">
            <News setProgress={this.setProgress} key="sport" pageSize={6} countries="in" topic="sport"/>
          </Route>
          <Route exact path="/Technology">
            <News setProgress={this.setProgress} key="tech" pageSize={6} countries="in" topic="tech"/>
          </Route>
         
        </Switch>
        </Router>
      </div>
    )
  }
}
